import DatePicker from "react-datepicker";
import { differenceInDays } from "date-fns";
import "react-datepicker/dist/react-datepicker.css";
import React, { useState, useEffect } from "react";
// import './App.css';
import DatePickerComponent from "./components/DatePickerComponent";
import InputComponent from "./components/InputComponent";
import OutputComponent from "./components/OutputComponent";
import "./styles.css";
import "./App.css";

function App() {
  const [startDate, setStartDate] = useState(new Date());
  const [endDate, setEndDate] = useState(new Date());

  const computeDifference = () => {
    return differenceInDays(endDate, startDate);
  };
  const [selectedDate, setSelectedDate] = useState(
    new Date().toISOString().split("T")[0]
  );
  const [days, setDays] = useState(0);
  const [calculatedDate, setCalculatedDate] = useState(selectedDate);

  useEffect(() => {
    const date = new Date(selectedDate);
    date.setDate(date.getDate() + parseInt(days, 10));
    setCalculatedDate(date.toISOString().split("T")[0]);
  }, [selectedDate, days]);

  return (
    <div className="App">
      <div className="top">
        <h1>React Datepicker</h1>
        <p>
          Two date functions are provided. The difference between two setDays
          and the date when days are added to a date.
        </p>
      </div>
      <div className="container">
        <DatePicker
          selected={startDate}
          onChange={(date) => setStartDate(date)}
        />
        <DatePicker selected={endDate} onChange={(date) => setEndDate(date)} />
        Difference: {computeDifference()} days
      </div>
      <div>
        <div className="container">
          <DatePickerComponent
            selectedDate={selectedDate}
            onDateChange={setSelectedDate}
          />
          <InputComponent onInputChange={setDays} />
          <OutputComponent calculatedDate={calculatedDate} />
        </div>
        <div className="bottom">Experimental, use at your own risk</div>
      </div>
    </div>
  );
}

export default App;
